* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

*, *:before, *:after {
  -moz-box-sizing: inherit;
  -webkit-box-sizing: inherit;
  -ms-box-sizing: inherit;
  box-sizing: inherit;
}

html, body, #root {
  height: 100%;
}

select {
  display: inline-block;
  margin: 0;
  outline: none;
  /* remove focus ring from Webkit */
  -webkit-appearance: none;
}
.select-wrap {
  position: relative;
}
.select-wrap:after, .select-wrap:before {
  /* content: "▲"; */
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  font-size: 60%;
  line-height: 30px;
  padding: 0 7px;
  font-size: 8px;
  color: #8a9aa4;
  pointer-events: none;
  -webkit-border-radius: 0 6px 6px 0;
  -moz-border-radius: 0 6px 6px 0;
  border-radius: 0 6px 6px 0;
}
.select-wrap::after {
    content: "▲";
    top: -4px;

}
.select-wrap::before {
  content: "▼";
  top: 4px;
}